import div2array from "../arrays/div2array.js";
import Upper from "./upprototype";
import Bottomproto from "./bottomproto";
import { Link } from "react-router-dom";
import "../cssfiles/links.css";

function Bottom() {
  return (
    <div className="bottom">
      <Link to="div1" style={{ textDecoration: "none", color: "black" }}>
        <Bottomproto
          title={div2array[0].title}
          image={div2array[0].image}
          data={div2array[0].data}
        />
      </Link>
      <Link to="div2" style={{ textDecoration: "none", color: "black" }}>
        <Bottomproto
          title={div2array[1].title}
          image={div2array[1].image}
          data={div2array[1].data}
        />
      </Link>

      <Bottomproto
        title={div2array[2].title}
        image={div2array[2].image}
        data={div2array[2].data}
      />
    </div>
  );
}

function Div3link3() {
  return (
    <div>
      <Upper
        title={div2array[2].title}
        data={div2array[2].data}
        image={div2array[2].image}
      />
      <Bottom />
    </div>
  );
}

export default Div3link3;
