import nature1 from "../images/nature1.jpg";
import nature2 from "../images/nature2.jpg";
import nature3 from "../images/nature3.jpg";
import "../cssfiles/div1.css";

export default function Div1() {
  return (
    <div className="div1">
      <div
        className="div1image1"
        style={{ backgroundImage: `url(${nature2})` }}
      >
        <h2>Nature itself is the best physician</h2>
        <p>Toursim / 20 August 2022</p>
      </div>
      <div className="twoimages">
        <div
          className="div1image2"
          style={{ backgroundImage: `url(${nature1})` }}
        ></div>
        <div
          className="div1image2"
          style={{ backgroundImage: `url(${nature3})` }}
        ></div>
      </div>
    </div>
  );
}
