import "../cssfiles/links.css";

function Upper(props) {
  return (
    <div className="upper">
      <h2>{props.title}</h2>
      <div
        style={{ backgroundImage: `url(${props.image})` }}
        className="upperimage"
      ></div>
      <p>{props.data}</p>
      <p>Latest Articles / 22 may 2022</p>
    </div>
  );
}

export default Upper;
