import "../cssfiles/links.css";
import div2array from "../arrays/div2array";

import Div1link1 from "../links/link1";

function Bottomproto(props) {
  return (
    <div className="bottompro">
      <h3>{props.title}</h3>
      <div
        className="bottomimage"
        style={{ backgroundImage: `url(${props.image})` }}
      ></div>
      <p>{props.data}</p>
      <p>Latest / 27 May 2222</p>
    </div>
  );
}

export default Bottomproto;
