import { Link } from "react-router-dom";
import "../cssfiles/nav.css";
export default function Nav() {
  return (
    <div className="nav">
      <p>
        The <span style={{ fontSize: "45px" }}>Siren</span>
      </p>
      <ul className="navul">
        <Link to="/" style={{ textDecoration: "none", color: "black" }}>
          <li>Home</li>
        </Link>
        <Link to="tollywood" style={{ textDecoration: "none", color: "black" }}>
          <li>Tollywood </li>
        </Link>
        <Link to="tourism" style={{ textDecoration: "none", color: "black" }}>
          <li>Tourism</li>
        </Link>
        <Link to="tech" style={{ textDecoration: "none", color: "black" }}>
          <li>Technology</li>
        </Link>
        <Link to="food" style={{ textDecoration: "none", color: "black" }}>
          <li>Food</li>
        </Link>

        <Link to="fitness" style={{ textDecoration: "none", color: "black" }}>
          <li>Fitness</li>
        </Link>
      </ul>
    </div>
  );
}
