import "../cssfiles/categoryproto.css";

function Leftproto(props) {
  return (
    <div className="left">
      <div
        className="leftimage"
        style={{ backgroundImage: `url(${props.image})` }}
      ></div>
      <div className="seconddiv">
        <h3>{props.title}</h3>
        <p>{props.data}</p>
        <p>{props.category} / 01 June 2022</p>
      </div>
    </div>
  );
}

export default Leftproto;
