import div3array from "../arrays/div3array.js";
import "../cssfiles/div3.css";
import { Link } from "react-router-dom";

function Div3prototype(props) {
  return (
    <div className="div3proto">
      <div
        className="div3image"
        style={{ backgroundImage: `url(${props.image})` }}
      ></div>
      <div className="div3div2">
        <h5>{props.title}</h5>
        <p style={{ fontSize: "17px" }}>{props.data}</p>
        <p style={{ fontSize: "17px" }}>Latest articles / 29 May 2022</p>
      </div>
    </div>
  );
}

function Advertisement() {
  return (
    <div className="ads">
      <h2>ADVERTISEMENT</h2>
    </div>
  );
}

function Div3() {
  return (
    <div className="div3real">
      <Link to="div4" style={{ textDecoration: "none", color: "black" }}>
        <Div3prototype
          key={div3array[0].id}
          title={div3array[0].title}
          data={div3array[0].data}
          image={div3array[0].image}
        />
      </Link>
      <Link to="div5" style={{ textDecoration: "none", color: "black" }}>
        <Div3prototype
          key={div3array[1].id}
          title={div3array[1].title}
          data={div3array[1].data}
          image={div3array[1].image}
        />
      </Link>

      <Link to="div6" style={{ textDecoration: "none", color: "black" }}>
        <Div3prototype
          key={div3array[2].id}
          title={div3array[2].title}
          data={div3array[2].data}
          image={div3array[2].image}
        />
      </Link>

      <Link to="div7" style={{ textDecoration: "none", color: "black" }}>
        <Div3prototype
          key={div3array[3].id}
          title={div3array[3].title}
          data={div3array[3].data}
          image={div3array[3].image}
        />
      </Link>

      <Link to="div8" style={{ textDecoration: "none", color: "black" }}>
        <Div3prototype
          key={div3array[4].id}
          title={div3array[4].title}
          data={div3array[4].data}
          image={div3array[4].image}
        />
      </Link>

      <Advertisement />
    </div>
  );
}

export default Div3;
