import div4array from "../arrays/div4array.js";
import "../cssfiles/div2.css";
import { Div2prototype } from "./div2.jsx";

function Div4() {
  return (
    <div className="div2real">
      <Div2prototype
        key={div4array[0].key}
        title={div4array[0].title}
        data={div4array[0].data}
        image={div4array[0].image}
      />

      <Div2prototype
        key={div4array[1].key}
        title={div4array[1].title}
        data={div4array[1].data}
        image={div4array[1].image}
      />

      <Div2prototype
        key={div4array[2].key}
        title={div4array[2].title}
        data={div4array[2].data}
        image={div4array[2].image}
      />
    </div>
  );
}

export default Div4;
