import React from "react";
import "../cssfiles/div2.css";
import div2array from "../arrays/div2array.js";
import { Link } from "react-router-dom";
import Div1link1 from "../links/link1";

function Div2prototype(props) {
  return (
    <div className="div2proto">
      <h3>{props.title}</h3>
      <div
        className="div2image"
        style={{ backgroundImage: `url(${props.image})` }}
      ></div>
      <p>{props.data}</p>
      <p>Latest / 27 May 2222</p>
    </div>
  );
}

function Div2() {
  return (
    <div className="div2real">
      <Link to="div1" style={{ textDecoration: "none", color: "black" }}>
        <Div2prototype
          key={div2array[0].id}
          title={div2array[0].title}
          data={div2array[0].data}
          image={div2array[0].image}
        />
      </Link>
      <Link to="div2" style={{ textDecoration: "none", color: "black" }}>
        <Div2prototype
          key={div2array[1].id}
          title={div2array[1].title}
          data={div2array[1].data}
          image={div2array[1].image}
        />
      </Link>
      <Link to="div3" style={{ textDecoration: "none", color: "black" }}>
        <Div2prototype
          key={div2array[2].id}
          title={div2array[2].title}
          data={div2array[2].data}
          image={div2array[2].image}
        />
      </Link>
    </div>
  );
}

export { Div2prototype };
export default Div2;
