import "../cssfiles/categoryproto.css";
import Ad from "../categoryprototypes/ads.jsx";
import Leftproto from "../categoryprototypes/leftproto.jsx";
import Rightproto from "../categoryprototypes/rightproto.jsx";
import foodarray from "../arrays/foodarray.js";

function Createlefttour(list) {
  return (
    <Leftproto
      key={list.id}
      data={list.data}
      category={list.category}
      image={list.image}
    />
  );
}

function Creatright(list) {
  return (
    <Rightproto key={list.key} category={list.category} image={list.image} />
  );
}

function Food() {
  return (
    <div className="maindiv">
      <div>
        <h2 className="protoh2">The Latest</h2>
        {foodarray.map(Createlefttour)}
      </div>
      <div>
        <h2 className="protoh2">Top posts</h2>
        {foodarray.map(Creatright)}
        <Ad />
      </div>
    </div>
  );
}

export default Food;
