import "../cssfiles/categoryproto.css";

function Rightproto(props) {
  return (
    <div className="right">
      <div
        className="rightimage"
        style={{ backgroundImage: `url(${props.image})` }}
      ></div>
      <div className="secondrightdiv">
        <h3>{props.title}</h3>
        <p>{props.category} / 01 June 2022</p>
      </div>
    </div>
  );
}

export default Rightproto;
